<template>
	<view class="main">
		<view class="hand">
			<image src="../../static/tabBarimg/dbleft.png" @click="blackbtn"></image>
		</view>
		<view class="show">
			<view class="sone">
				<view class="sonefoot">
					<view class="sfone">
						<view class="sfoneleft">
							<view class="sfoneleftfirst">{{this.projectName}}-{{this.areaName}}</view>
							<view :class="beitype=='0'?'sfoneleftsun':'sfoneleftsun1'" @click="bbtn">备注：{{this.marks}}</view>
							<!-- <view :class="beitype=='1'?'sfoneleftsun1':'sfoneleftsun'" @click="bbtn(1)">备注：{{this.marks}}</view> -->
						</view>
						<image src="../../static/tabBarimg/up.png" v-show="beitype==1&&this.marks.length>18" @click="bbtn"></image>
						<image src="../../static/tabBarimg/down.png" v-show="beitype==0&&this.marks.length>18" @click="bbtn"></image>
						<view class="sfoneright" @click="shuxin">
							刷新
						</view>
						
					</view>
					<view class="sftwo">
						<view class="sftwoleft">
							<view class="sftwotap" style="margin-bottom: 10upx;">
								<view class="sleftone">在线</view>
								<view class="stxt" v-show="areaType==0">充电桩已联网</view>
								<view class="stxt" v-show="areaType==1">充电柜已联网</view>
							</view>
							<view class="sftwotap">
								<view class="slefttwo">离线</view>
								<view class="stxt" v-show="areaType==0">充电桩已断网</view>
								<view class="stxt" v-show="areaType==1">充电柜已断网</view>
							</view>
							<view style="display: flex;margin-top: 10upx;">
								<view>
									<view class="sftwotap">
										<view class="sleftthree port0" ></view>
										<view class="stxt">空闲</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port1"></view>
										<view class="stxt">充电中</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port2"></view>
										<view class="stxt">未充占用</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port3"></view>
										<view class="stxt">充满占用</view>
									</view>
								</view>
								
								<view style="margin-left: 50upx;">
									<view class="sftwotap">
										<view class="sleftthree port4"></view>
										<view class="stxt">端口故障</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port5"></view>
										<view class="stxt">浮充</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port88"></view>
										<view class="stxt">绑定端口</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port99"></view>
										<view class="stxt">禁用端口</view>
									</view>
								</view>
							</view>
							<view class="slzhan" @click="slbtn(1)" v-show="slshow==2">
								<view class="slzhanone">展开</view>
								<view class="slzhantwo">
									<image src="../../static/tabBarimg/down.png"></image>
								</view>
							</view>
							<view style="display: flex;" v-show="slshow==1">
								<view>
									<view class="sftwotap">
										<view class="sleftthree port6" ></view>
										<view class="stxt">存储器损坏</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port7"></view>
										<view class="stxt">安全门卡住</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port8"></view>
										<view class="stxt">保险丝烧断</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port15"></view>
										<view class="stxt">刷卡芯片损坏</view>
									</view>
									
									
								</view>
								
								<view style="margin-left: 50upx;">
									<view class="sftwotap">
										<view class="sleftthree port9"></view>
										<view class="stxt">继电器粘连</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port10"></view>
										<view class="stxt">门磁芯片损坏</view>
									</view>
									<view class="sftwotap">
										<view class="sleftthree port13"></view>
										<view class="stxt">预检负载短路</view>
									</view>
									
								</view>
							</view>
							<view class="slzhan" @click="slbtn(2)" v-show="slshow==1">
								<view class="slzhanone">收起</view>
								<view class="slzhantwo">
									<image src="../../static/tabBarimg/up.png"></image>
								</view>
							</view>
						</view>
						<view class="sftworight">
							<view class="srbtn" @click="scan" v-show="deviceSettings==1">添加设备</view>
							<view class="srbtn" @click="ratebtn" v-show="deviceSettings==1">费率绑定</view>
							<!-- <view class="srbtn" @click="nameshowopen" v-show="deviceSettings==1">修改信息</view> -->
							<view class="srbtn" @click="flgo" >显示费率</view>
							<view class="srbtn" @click="mjgo" >门禁设备</view>
							<view class="srbtn" @click="lbgo" >漏保主机</view>
							<view class="srbtn" @click="gdgo" v-show="deviceSettings==1">查看更多</view>
							<!-- <view class="srbtn" @click="czgo" >重置坐标</view> -->
							<!-- <view class="srbtn" style="background-color: red;" @click="delbtn" v-show="isProportion==1&&blist.length==0">删除充电区</view> -->
						</view>
					</view>
				</view>
				<view class="stwo">
					<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
					<van-list v-model="loading" :offset="300" :finished="finished" finished-text="没有更多了" @load="loadMore">
					<van-cell v-for="(item, index) in blist" :key="index" >
					<view class="stwotap">
						<view class="stwotaphand">
							<view class="stapleft">
								<view class="stfone" v-show="item.state=='联网'">在线</view>
								<view class="stfone1" v-show="item.state=='断网'">离线</view>
								<view class="stftwo">{{item.code}}</view>
							</view>
							<view class="dknum" >
								<view v-for="(item, index) in item.dknumber" :key="index">
									<view class="sleftthree port0" v-show="item==0"></view>
									<view class="sleftthree port1" v-show="item==1"></view>
									<view class="sleftthree port2" v-show="item==2"></view>
									<view class="sleftthree port3" v-show="item==3"></view>
									<view class="sleftthree port4" v-show="item==4"></view>
									<view class="sleftthree port6" v-show="item==6"></view>
									<view class="sleftthree port7" v-show="item==7"></view>
									<view class="sleftthree port8" v-show="item==8||item==11"></view>
									<view class="sleftthree port9" v-show="item==9||item==14"></view>
									<view class="sleftthree port10" v-show="item==10"></view>
									<view class="sleftthree port5" v-show="item==5"></view>
									<view class="sleftthree port0" v-show="item==12"></view>
									<view class="sleftthree port13" v-show="item==13"></view>
									<view class="sleftthree port15" v-show="item==15"></view>
									<view class="sleftthree port88" v-show="item==88"></view>
									<view class="sleftthree port99" v-show="item==99"></view>
									<view class="sleftthree port2" v-show="item==170"></view>
								</view>
							</view>
							<view class="stapz">
								<!-- 2G | 50% -->
								<span class='color1' v-show='item.signals&&(item.signals>0&&item.signals<26)'>{{item.signals}}%</span>
								<span class='color2' v-show='item.signals&&(item.signals>25&&item.signals<51)'>{{item.signals}}%</span>
								<span class='color3' v-show='item.signals&&(item.signals>50&&item.signals<76)'>{{item.signals}}%</span>
								<span class='color4' v-show='item.signals&&(item.signals>75&&item.signals<101)'>{{item.signals}}%</span>
								 | {{item.module}}
							</view>
							<view class="stapright">
								<!-- <image src="../../static/tabBarimg/look.png" class="imgone" @click="xqshowopen(item.id,item.deviceId,item.portState)"></image> -->
								<view class="imgone" @click="xqshowopen(item.id,item.deviceId,item.portState,item.dknumber1)">详情</view>
								<image src="../../static/tabBarimg/tapdel.png" class="imgtwo" @click="detailnum(item.id)"></image>
							</view>
						</view>
						<view class="stwotapfoot">
							<view style='width:26%;'>当前温度:{{item.sidetemp}}</view>
							<view style="width: 23%;">
								<view class="dno" @click="dnobtn(item.deviceId)" 
									v-if="item.deviceId.substring(item.deviceId.length - 2, item.deviceId.length)=='05'||item.deviceId.substring(item.deviceId.length - 2, item.deviceId.length)=='06'||item.deviceId.substring(item.deviceId.length - 2, item.deviceId.length)=='07'">
									端口码管理</view>
							</view>
							
							<view style="width: 27%;">
								<view v-show="item.strengthStr!=100&&item.strengthStr!='无'" >
									
									<span class='color1' v-show='item.strengthStr>0&&(item.strengthStr<25||item.strengthStr==25)'>{{item.strengthStr}}%</span>
									<span class='color2' v-show='item.strengthStr>25&&(item.strengthStr<50||item.strengthStr==50)'>{{item.strengthStr}}%</span>
									<span class='color3' v-show='item.strengthStr>50&&(item.strengthStr<75||item.strengthStr==75)'>{{item.strengthStr}}%</span>
									<span class='color4' v-show='item.strengthStr>75&&(item.strengthStr<100||item.strengthStr==100)'>{{item.strengthStr}}%</span>
									| LORA
								</view>
								<view v-show="item.netType!=0" >
									|<span class='color5' v-show='item.netType==2'>FSK(主)</span>
									<span class='color6' v-show='item.netType==1'>FSK(分)</span>
								</view>
							</view>
							
							<view class="dno"  @click="uprem(item.id,item.remark)">
								修改备注
							</view>
						</view>
						<view class="stwotapfoot" v-show="item.remark">
							<view>备注：{{item.remark}}</view>
						</view>
						<view class="dktap" v-show="item.dknumber1">
							<view>柜门状态:</view>
							<view class="dknum1" >
								<view v-for="(item, index) in item.dknumber1" :key="index">
									<view class="sleftthree port0" v-show="item==0&&item!=''"></view>
									<view class="sleftthree port4" v-show="item==1"></view>
									
								</view>
								
							</view>
						</view>
						<view class="zuwang" v-show="item.netType==2&&item.netIsSuc==0">
							<view class="zwone" @click="zwbtn(item.id)">组网</view>
						</view>
						
					</view>
				
					</van-cell>
					</van-list>
					</van-pull-refresh>	
				</view>
			</view>
			
			<!-- //设备详情弹窗提示 -->
			<van-popup v-model="xqshow">
				<view class="tannei">
					<view class="tnhand">
						<view class="tntxt">设备详情</view>
						<image src="../../static/tabBarimg/close.png" @click="xqshowclose"></image>
					</view>
					<view class="tnshow">
						<view>主机信息</view>
						<view class="tnshowone" @click="shuabtn" v-show="Verification">刷新</view>
						<view class="tnshowone1" v-show="!Verification">{{timer}}s</view>
					</view>
					<view class="tnfoot">
						<view class="tntap" v-show="host.simCodes">
							<view class="tntapleft" style="width: 100upx;">sim号:</view>
							<view class="tntaoright">{{host.simCodes}}</view>
						</view>
						<view class="tntap" v-show="host.hostType">
							<view class="tntapleft">主机类型:</view>
							<view class="tntaoright">{{host.hostType}}</view>
						</view>
						<view class="tntap" v-show="host.version">
							<view class="tntapleft">固件版本:</view>
							<view class="tntaoright">{{host.version}}</view>
						</view>
						<view class="tntap" v-show="host.packageName">
							<view class="tntapleft">套餐名称:</view>
							<view class="tntaoright">{{host.packageName}}</view>
						</view>
						<view class="tntap" v-show="host.netFrequency">
							<view class="tntapleft">无线频率:</view>
							<view class="tntaoright">{{host.netFrequency}}</view>
						</view>
						<view class="tntap" v-show="host.state">
							<view class="tntapleft">卡状态:</view>
							<view class="tntaoright tcolor00" v-show="host.state=='00'">正常</view>
							<view class="tntaoright tcolor0" v-show="host.state=='0'">未知</view>
							<view class="tntaoright tcolor88" v-show="host.state=='01'">单向停机</view>
							<view class="tntaoright tcolor88" v-show="host.state=='02'">停机</view>
							<view class="tntaoright" v-show="host.state=='03'">预销号</view>
							<view class="tntaoright tcolor99" v-show="host.state=='04'">销号/拆机</view>
							<view class="tntaoright" v-show="host.state=='05'">过户</view>
							<view class="tntaoright" v-show="host.state=='06'">休眠</view>
							<view class="tntaoright" v-show="host.state=='07'">待机</view>
							<view class="tntaoright" v-show="host.state=='08'">已停用</view>
							<view class="tntaoright" v-show="host.state=='09'">库存</view>
							<view class="tntaoright" v-show="host.state=='10'">已失效</view>
							<view class="tntaoright" v-show="host.state=='11'">违章停机</view>
							<view class="tntaoright" v-show="host.state=='12'">挂失</view>
							<view class="tntaoright" v-show="host.state=='13'">用户报停</view>
							<view class="tntaoright" v-show="host.state=='14'">断网</view>
							<view class="tntaoright tcolor88" v-show="host.state=='77'">待复机</view>
							<view class="tntaoright tcolor88" v-show="host.state=='88'">待停机</view>
							<view class="tntaoright tcolor99" v-show="host.state=='99'">不存在</view>
						</view>
						<view class="tntap" v-show="host.activeTime">
							<view class="tntapleft">激活时间:</view>
							<view class="tntaoright">{{host.activeTime}}</view>
						</view>
						<view class="tntap" v-show="host.expireTime">
							<view class="tntapleft">过期时间:</view>
							<view class="tntaoright">{{host.expireTime}}</view>
						</view>
						<view class="tntap" v-show="host.useFlow">
							<view class="tntapleft" style="width: 300upx;">本月已用流量（MB）:</view>
							<view class="tntaoright">{{host.useFlow}}</view>
						</view>
					</view>
					<view class="tnshow">从机信息</view>
					<view class="tnfoot">
						<view class="tntap" v-show="equipment.deviceType">
							<view class="tntapleft">设备类型:</view>
							<view class="tntaoright">{{equipment.deviceType}}</view>
						</view>
						<view class="tntap" v-show="equipment.version">
							<view class="tntapleft">固件版本号:</view>
							<view class="tntaoright">{{equipment.version}}</view>
						</view>
						<view class="tntap" v-show="equipment.boardVersion">
							<view class="tntapleft">电源版本号:</view>
							<view class="tntaoright">{{equipment.boardVersion}}</view>
						</view>
						<view class="tntap" v-show="equipment.voltage">
							<view class="tntapleft">电压:</view>
							<view class="tntaoright">{{equipment.voltage}}</view>
						</view>
						<view class="tntap" v-show="equipment.longTim">
							<view class="tntapleft">最大充电时长:</view>
							<view class="tntaoright">{{equipment.longTime}}小时</view>
						</view>
						<view class="tntap" v-show="equipment.longPower">
							<view class="tntapleft">最大充电功率:</view>
							<view class="tntaoright">{{equipment.longPower}}瓦</view>
						</view>
						<view class="tntap" v-show="equipment.time">
							<view class="tntapleft" style="width: 250upx;">设备最后联网时间:</view>
							<view class="tntaoright">{{equipment.time}}</view>
						</view>
					</view>
					
					<van-tabs v-model="active">
						<van-tab title="设备端口" name="a">
							<!-- <view class="tnshow">设备端口</view> -->
							<view style="font-size: 28upx;color: red;margin-left: 18upx;">1.每次通电将在一分钟后自动断电</view>
							<view  style="font-size: 28upx;color: red;margin-left: 18upx;">2.此操作不影响正常订单</view>
							<view  style="font-size: 28upx;color: red;margin-left: 18upx;">3.请勿给正常充电的端口结束充电</view>
							<view class="cdk">
								<view v-for="(item, index) in celist" :key="index"  @click="cdkbtn(item.numname)" >
									<view :class="item.btn=='0'?'cdkwai port0':'cdkwai1 port0'" v-show="item.numtype==0||item.numtype==12">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port1':'cdkwai1 port1'" v-show="item.numtype==1">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port2':'cdkwai1 port2'" v-show="item.numtype==2">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port3':'cdkwai1 port3'" v-show="item.numtype==3">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port4':'cdkwai1 port4'" v-show="item.numtype==4">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port5':'cdkwai1 port5'" v-show="item.numtype==5">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port6':'cdkwai1 port6'" v-show="item.numtype==6">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port7':'cdkwai1 port7'" v-show="item.numtype==7">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port8':'cdkwai1 port8'" v-show="item.numtype==8||item.numtype==11">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port9':'cdkwai1 port9'" v-show="item.numtype==9||item.numtype==14">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port10':'cdkwai1 port10'" v-show="item.numtype==10">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port13':'cdkwai1 port13'" v-show="item.numtype==13">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port15':'cdkwai1 port15'" v-show="item.numtype==15">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port88':'cdkwai1 port88'" v-show="item.numtype==88">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port99':'cdkwai1 port99'" v-show="item.numtype==99">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port2':'cdkwai1 port2'" v-show="item.numtype==170">
										{{item.numname}}
									</view>
								</view>
							</view>
						</van-tab>
						<van-tab title="设备柜门" v-if="doorState" name="b">
							<view style="font-size: 28upx;color: red;margin-left: 18upx;">1.强制打开柜门后，设备下发开门指令，订单结束，订单结束原因为强制打开柜门。</view>
							<view class="cdk">
								<view v-for="(item, index) in celist" :key="index"  @click="cdkbtn(item.numname)" >
									<view :class="item.btn=='0'?'cdkwai port0':'cdkwai1 port0'" v-show="item.numtype==0||item.numtype==12">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port1':'cdkwai1 port1'" v-show="item.numtype==1">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port2':'cdkwai1 port2'" v-show="item.numtype==2">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port3':'cdkwai1 port3'" v-show="item.numtype==3">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port4':'cdkwai1 port4'" v-show="item.numtype==4">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port5':'cdkwai1 port5'" v-show="item.numtype==5">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port6':'cdkwai1 port6'" v-show="item.numtype==6">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port7':'cdkwai1 port7'" v-show="item.numtype==7">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port8':'cdkwai1 port8'" v-show="item.numtype==8||item.numtype==11">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port9':'cdkwai1 port9'" v-show="item.numtype==9||item.numtype==14">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port10':'cdkwai1 port10'" v-show="item.numtype==10">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port13':'cdkwai1 port13'" v-show="item.numtype==13">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port15':'cdkwai1 port15'" v-show="item.numtype==15">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port88':'cdkwai1 port88'" v-show="item.numtype==88">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port99':'cdkwai1 port99'" v-show="item.numtype==99">
										{{item.numname}}
									</view>
									<view :class="item.btn=='0'?'cdkwai port2':'cdkwai1 port2'" v-show="item.numtype==170">
										{{item.numname}}
									</view>
								</view>
							</view>
						</van-tab>
					</van-tabs>
					
					<view class="cdkkat" v-show="active === 'a'">
						<view :class="atype==0?'cdkkatone':'cdkkatone1'" @click="cdkopen">
							测试通电
						</view>
						<view :class="btype==0?'cdkkattwo':'cdkkattwo1'" @click="cdkclose">
							结束通电
						</view>
					</view>
					<view class="cdkkat" v-show="active === 'b'">
						<van-button class="cdkkattwo" style="padding: unset; font-size: 16px;" :disabled="!tdnum" color="#910012" @click="showDialog = true">强制开门</van-button>
					</view>
					
				</view>
			</van-popup>
			<!-- //查看更多 -->
			<van-popup v-model="gdshow">
				<view class="tannei">
					<view class="tnhand">
						<image src="../../static/tabBarimg/close.png" @click="gdshowclose"></image>
					</view>
					<view class="gdtype">
						<view class="gdshowbtn" @click="czgo">
							清除坐标
						</view>
						<view class="gdshowbtn" @click="nameshowopen">
							修改信息
						</view>
					</view>
				</view>
			</van-popup>
			<!-- //参数设置弹窗提示 -->
			<van-popup v-model="cshow">
				<view class="tannei">
					<view class="tnhand">
						<view class="tntxt">参数设置</view>
						<image src="../../static/tabBarimg/close.png" @click="cshowclose"></image>
					</view>
					<view class="tnone">
						<view class="tnonetap">
							<view class="toneleft">
								最大充电功率
							</view>
							<input placeholder="如：1瓦" v-model="longPower"/>
						</view>
						<view class="tnonetap">
							<view class="toneleft">
								最大充电时长
							</view>
							<input placeholder="如：1小时" v-model="longTime"/>
						</view>
					</view>
					<view class="tntwo" @click="csbtn">确认提交</view>
				</view>
			</van-popup>
			<!-- //修改名称弹窗提示 -->
			<van-popup v-model="nameshow">
				<view class="tannei">
					<view class="tnhand">
						<view class="tntxt">修改信息</view>
						<image src="../../static/tabBarimg/close.png" @click="nameshowclose"></image>
					</view>
					<view class="tnone">
						<view class="tnoneshow">
							<view class="tnonetxt" >充电区名：</view>
							<input placeholder="请输入新名称" v-model="name"/>
						</view>
						<view class="tnoneshow">
							<view class="tnonetxt">备注：</view>
							<textarea placeholder="请输入新备注" v-model="marks1" maxlength="50"/>
						</view>
					</view>
					<view class="tntwo" @click="namebtn">确认提交</view>
				</view>
			</van-popup>
			<!-- //修改备注弹窗提示 -->
			<van-popup v-model="remshow">
				<view class="tannei">
					<view class="tnhand">
						<view class="tntxt">修改备注</view>
						<image src="../../static/tabBarimg/close.png" @click="remshowclose"></image>
					</view>
					<view class="tnone">
						<view class="tnoneshow">
							<view class="tnonetxt">备注：</view>
							<textarea placeholder="请输入备注" v-model="upremarks" maxlength="10"/>
						</view>
					</view>
					<view class="tntwo" @click="rembtn">确认提交</view>
				</view>
			</van-popup>
			<!-- 费率显示 -->
			<van-popup v-model="feilv">
				<view class="sfour">
					<view class="stwohand">
						费率详情<image src="../../static/tabBarimg/close.png" @click="showclose"></image>
					</view>
					<view class="stwofoot">
						
						<view class="sftwo">
							<view class="sftwofoot" v-show="monthlyList.length>0">
								<view class="montleft">
									包月：
								</view>
								<view class="montright">
									<view class="montone" v-for="(item, index) in monthlyList" :key="index">
										<view v-show="item.types=='A1'">
											<view>{{item.standard}}元/{{item.num}}小时/月</view>
										</view>
										<view v-show="item.types=='A3'">
											<view >
												{{item.standard}}元/{{item.num}}次/月
											</view>
										</view>
										<view v-show="item.types=='A4'">
											<view v-show="item.minPower!=item.maxPower">
												{{item.minPower}}瓦--{{item.maxPower}}瓦{{item.standard}}元/月(共{{item.num}}小时)
											</view>
											<view v-show="item.minPower==item.maxPower">
												{{item.maxPower}}瓦以上{{item.standard}}元/月(共{{item.num}}小时)
											</view>
										</view>
										<view v-show="item.types=='A6'">
											<view v-show="item.minPower!=item.maxPower">
												{{item.minPower}}瓦--{{item.maxPower}}瓦{{item.standard}}元/月(共{{item.num}}次)
											</view>
											<view v-show="item.minPower==item.maxPower">
												{{item.maxPower}}瓦以上{{item.standard}}元/月(共{{item.num}}次)
											</view>
										</view>
										<view v-show="item.types=='A5'">
											<view v-show="item.minPower!=item.maxPower">
												{{item.minPower}}瓦--{{item.maxPower}}瓦{{item.standard}}元/月({{item.num}}小时/天)
											</view>
											<view v-show="item.minPower==item.maxPower">
												{{item.maxPower}}瓦以上{{item.standard}}元/月({{item.num}}小时/天)
											</view>
										</view>
										<view v-show="item.types=='A7'">
											<view v-show="item.minPower!=item.maxPower">
												{{item.minPower}}瓦--{{item.maxPower}}瓦{{item.standard}}元/月({{item.num}}次/天)
											</view>
											<view v-show="item.minPower==item.maxPower">
												{{item.maxPower}}瓦以上{{item.standard}}元/月({{item.num}}次/天)
											</view>
										</view>
									</view>
									<view class="montone" v-show="oneremarks">
										备注:{{oneremarks}}
									</view>
									<view class="montone">
										允许同时充电次数:{{limitNum}}
									</view>
									<view class="montone" v-show="areaType==1">占位费免费时长(小时):{{monthlyOccupyFree/60}}</view>
									<!-- <view>占位费封顶金额:￥{{monthlyOccupyMax}}元</view> -->
									<view class="montone" v-show="areaType==1">占位费率:{{monthlyOccupyRate}}</view>
									
								</view>
							</view>
							<view class="sftwofoot" v-show="oftenList.length>0">
								<view class="montleft">
									常用：
								</view>
								<view class="montright">
									<view class="montone" v-for="(item, index) in oftenList" :key="index">
										<view v-show="item.types=='B2'">
											电费:{{item.standard}}元/度
										</view>
										<view v-show="item.types=='B5'">
											电费:{{item.standard}}元/度
										</view>
										<view v-show="item.types=='B1'">
											<view v-show="item.minPower!=item.maxPower">
											{{item.minPower}} ~ {{item.maxPower}}瓦- {{item.standard}}元/小时
											</view>
											<view v-show="item.minPower==item.maxPower">{{item.maxPower}}瓦以上- {{item.standard}}元/小时</view>
										</view>
										<view v-show="item.types=='B3'">
											<view v-show="item.minPower!=item.maxPower">
											{{item.minPower}} ~ {{item.maxPower}}瓦- {{item.standard}}元/次
											</view>
											<view v-show="item.minPower==item.maxPower">{{item.maxPower}}瓦以上- {{item.standard}}元/次</view>
										</view>
										<view v-show="item.types=='B4'">
											<view v-show="item.minPower!=item.maxPower">
											{{item.minPower}} ~ {{item.maxPower}}瓦- 1元/{{item.standard}}小时
											</view>
											<view v-show="item.minPower==item.maxPower">{{item.maxPower}}瓦以上- 1元/{{item.standard}}小时</view>
										</view>
										
									</view>
									<view class="montone">
										最低消费:{{oftenminMoney}}元
									</view>
									<view class="montone" v-show="oftentypes=='B2'">
										电损率:{{oftenlossRate}}
									</view>
									<view class="montone" v-show="tworemarks">
										备注:{{tworemarks}}
									</view>
									<view class="montone" v-show="oftentypes=='B2'&&twonum">
										服务费:{{twonum}}元/小时
									</view>
									<view class="montone" v-show="oftentypes=='B5'&&twonum">
										服务费:{{twonum}}元/度
									</view>
									<view class="montone" v-show="areaType==1">占位费免费时长(小时):{{oftenOccupyFree/60}}</view>
									<view class="montone" v-show="areaType==1">占位费封顶金额:￥{{oftenOccupyMax}}元</view>
									<view class="montone" v-show="areaType==1">占位费率:{{oftenOccupyRate}}</view>
								</view>
							</view>
							<view class="sftwofoot" v-show="temporaryList.length>0">
								<view class="montleft">
									临时：
								</view>
								<view class="montright">
									<view class="montone" v-for="(item, index) in temporaryList" :key="index">
										<view v-show="item.minPower!=item.maxPower">{{item.minPower}} ~ {{item.maxPower}}瓦- {{item.standard}}元/时</view>
										<view v-show="item.minPower==item.maxPower">{{item.maxPower}}瓦以上- {{item.standard}}元/时</view>
									</view>
									<view class="montone">
										允许退费:<span v-show="isFund==0">不允许</span><span v-show="isFund==1">允许</span>
									</view>
									<view class="montone">
										最低消费:{{lsminMoney}}元
									</view>
									<view class="montone">
										临时金额:{{first}}元/{{second}}元/{{third}}元/{{fourth}}元
									</view>
									<view class="montone" v-show="threeremarks">
										备注:{{threeremarks}}
									</view>
									<view class="montone" v-show="areaType==1">占位费免费时长(小时):{{temporaryOccupyFree/60}}</view>
									<view class="montone" v-show="areaType==1">占位费封顶金额:￥{{temporaryOccupyMax}}元</view>
									<view class="montone" v-show="areaType==1">占位费率:{{temporaryOccupyRate}}</view>
								</view>
							</view>
						</view>
					</view>
				</view>
			</van-popup>

			<!-- 带计时器的弹窗 -->
			<time-dialog :show.sync="showDialog" @sure="forcedOpening"></time-dialog>
		</view>
	</view>
</template>

<script>
	 
	import timeDialog from '@/components/timer-dialog/index.vue'
	import Toast from '../../wxcomponents/vant/toast/toast';
	import Dialog from '../../wxcomponents/vant/dialog/dialog';
	export default {
		components: { timeDialog },
		mounted() {
			
		},

		data() {
			return {
				doorState: null,
				showDialog: false,
				active: 'a',
				xqshow:false,//设备详情弹窗
				cshow:false,//参数设置弹窗
				nameshow:false,//修改名称弹窗
				areaId:'',//充电区ID
				projectId:'',
				name:'',//名称
				marks:'',//备注
				marks1:'',//备注
				longPower:'',//最大充电功率
				longTime:'',//最大充电时间
				blist:[],//设备列表
				loading: false,
				finished: false,
				refreshing: false,
				pageNum: 1, //页码
				pageSize: 10, //每页显示的条数
				host:{},//主机信息
				equipment:{},//设备信息
				deviceId:'',//设备编号
				isProportion:'',
				deviceSettings:0,
				monthlyList:[],//包月数据
				oftenList:[],//常用数据
				temporaryList:[],//临时数据
				feilv:false,//费率显示
				firstone:false,
				oftenminMoney:'',//常用最低消费
				oftenlossRate:'',//常用电损率
				twonum:'',//服务费
				oftentypes:'',
				isFund:'',//是否允许退费
				lsminMoney:'',//临时最低消费
				first:'',
				second:'',
				third:'',
				fourth:'',
				oneremarks:'',//包月备注
				tworemarks:'',//常用备注
				threeremarks:'',//临时备注
				projectName:'',//小区名
				areaName:'',//充电区名
				beitype:0,//备注信息展开
				gdshow:false,
				limitNum:1,//允许同时充电次数
				addNew:0,
				slshow:2,
				areaType:0,//0充电桩   1充电柜
				oftenOccupyFree:0,//常用免费时长-小时（充电柜）
				oftenOccupyMax:0,//常用占位费封顶金额
				oftenOccupyRate:0,//常用占位费率（充电柜）
				temporaryOccupyFree:0,//临时免费时长-小时（充电柜）
				temporaryOccupyMax:0,//临时占位费封顶金额
				temporaryOccupyRate:0,//临时占位费率（充电柜）
				monthlyOccupyFree:0,//包月免费时长-小时（充电柜）
				monthlyOccupyMax:0,//包月占位费封顶金额
				monthlyOccupyRate:0,//包月占位费率（充电柜）
				sbdeviceId:'',//刷新 16位设备号
				sbid:'',//刷新设备id
				Verification: true, //通过v-show控制显示获取还是倒计时
				timer:3,
				remshow:false,//修改备注弹窗
				remid:'',//设备id
				upremarks:'',//修改设备备注
				celist:'',//测试上电端口列表
				tdnum:'',//测试通电端口号
				atype:0,
				btype:0,
			}
		},
		onShow() {
			this.firstone=false
		},
		onLoad(options) {
			this.projectId=options.projectId
			this.areaId=options.id
			this.marks=options.marks
			this.marks1=options.marks
			this.isProportion=options.isProportion
			this.deviceSettings=options.deviceSettings
			this.projectName=options.projectName
			this.areaName=options.areaName
			this.name=options.areaName
			this.addNew=options.addNew
			this.areaType=options.areaType
		},
		methods: {
			// 强制开门
			forcedOpening() {
				let data={
					port:this.tdnum,
					deviceId:this.sbdeviceId
				}
				this.$base.request1('manager/equipment/openDoor', 'POST', data)
					.then(res => {
						if(res.data.code=='200'){
							this.$toast.success('强制开门成功')
							return
						}
						this.$toast.fail(res.data.msg)
					})
			},
			//返回上一页
			blackbtn() {
				uni.navigateBack()
			},
			//关闭设备详情弹窗
			xqshowclose() {
				this.xqshow = false
			},
			slbtn(type){
				this.slshow=type
			},
			//打开设备详情弹窗
			xqshowopen(id,deviceId,portState,dknumber1) {
				console.log(dknumber1, 'dknumber1=======');
				this.doorState = dknumber1
				this.sbid=id
				this.sbdeviceId=deviceId
				this.xqshow = true
				// 初始化选中端口
				this.tdnum = ''
				this.$base.request1('manager/equipment'+'/'+id, 'GET')
				.then(res => {
					this.host={}
					this.equipment={}
					if(res.data.data.host){
						this.host=res.data.data.host[0]
					}
					if(res.data.data.equipment){
						this.equipment=res.data.data.equipment[0]
					}
				})
				.catch(err => {
			
				})
				this.celist=portState.split(',')
				var klist=[]
				for(var i=0;i<this.celist.length;i++){
					var dk={}
					dk.numname=i+1
					dk.numtype=this.celist[i]
					dk.btn=0
					klist.push(dk)
				}
				this.celist=klist
			},
			cdkbtn(numname){
				this.tdnum=numname
				for(var i=0;i<this.celist.length;i++){
					if(numname==this.celist[i].numname){
						this.celist[i].btn=1
					}else{
						this.celist[i].btn=0
					}
				}
			},
			//设备端口测试通电
			cdkopen(){
				if(this.atype==0){
					if(!this.tdnum){
						this.$toast.fail('请选择端口')
						return
					}
					let data={
						port:this.tdnum,
						deviceId:this.sbdeviceId,
						control:'01'
					}
					this.atype=1
					this.$base.request('charge/testCharge', 'POST',data)
						.then(res => {
							if(res.data.code=='200'){
								this.$toast.success('操作成功')
								setTimeout(() => {
									this.atype=0
								}, 2000)
							}else{
								this.$toast.fail(res.data.msg)
							}
						})
						.catch(err => {
					
						})
				}
				
			},
			//设备端口结束通电
			cdkclose(){
				if(this.btype==0){
					if(!this.tdnum){
						this.$toast.fail('请选择端口')
						return
					}
					let data={
						port:this.tdnum,
						deviceId:this.sbdeviceId,
						control:'00'
					}
					Dialog.confirm({
						title: '提示',
						message: '请注意当前停止设备是否有用户正在使用，确认继续操作？'
					}).then(() => {
						this.btype=1
						this.$base.request('charge/testCharge', 'POST',data)
							.then(res => {
								if(res.data.code=='200'){
									this.$toast.success('操作成功')
									setTimeout(() => {
										this.btype=0
									}, 2000)
									
								}else{
									this.$toast.fail(res.data.msg)
								}
							})
							.catch(err => {
						
							})
					}).catch(() => {
						// on cancel
					});
					
				}
				
			},
			//费率数据
			flshow(){
				this.$base.request1('manager/area'+'/'+this.areaId, 'GET')
					.then(res => {
						if(res.data.code==200){
							var item=res.data.data
							if(item.monthlyList){
								if(item.monthlyList.length>0){
									this.monthlyList=item.monthlyList
									this.oneremarks=item.monthlyList[0].remarks
									this.limitNum=item.monthlyList[0].limitNum
									this.monthlyOccupyFree=item.monthlyList[0].occupyFree
									this.monthlyOccupyMax=item.monthlyList[0].occupyMax
									this.monthlyOccupyRate=item.monthlyList[0].occupyRate
								}else{
									this.monthlyList=[]
								}
							}else{
								this.monthlyList=[]
							}
							if(item.oftenList){
								if(item.oftenList.length>0){
									this.oftenList=item.oftenList
									this.oftenminMoney=item.oftenList[0].minMoney
									this.oftenlossRate=item.oftenList[0].lossRate
									this.twonum=item.oftenList[0].num/100
									this.oftentypes=item.oftenList[0].types
									this.tworemarks=item.oftenList[0].remarks
									
									this.oftenOccupyFree=item.oftenList[0].occupyFree
									this.oftenOccupyMax=item.oftenList[0].occupyMax
									this.oftenOccupyRate=item.oftenList[0].occupyRate
								}else{
									this.oftenList=[]
								}
							}else{
								this.oftenList=[]
							}
							if(item.temporaryList){
								if(item.temporaryList.length>0){
									this.temporaryList=item.temporaryList
									this.isFund=item.temporaryList[0].isFund
									this.lsminMoney=item.temporaryList[0].minMoney
									this.first=item.temporaryList[0].first
									this.second=item.temporaryList[0].second
									this.third=item.temporaryList[0].third
									this.fourth=item.temporaryList[0].fourth
									this.threeremarks=item.temporaryList[0].remarks
									
									this.temporaryOccupyFree=item.temporaryList[0].occupyFree
									this.temporaryOccupyMax=item.temporaryList[0].occupyMax
									this.temporaryOccupyRate=item.temporaryList[0].occupyRate
								}else{
									this.temporaryList=[]
								}
							}else{
								this.temporaryList=[]
							}
							
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			//关闭参数设置弹窗
			cshowclose() {
				this.cshow = false
			},
			//打开参数设置弹窗
