				this.cshow = true
			},
			//关闭修改名称弹窗
			nameshowclose() {
				this.nameshow = false
			},
			//关闭修改备注弹窗
			remshowclose(){
				this.remshow=false
			},
			//打开修改名称弹窗
			nameshowopen() {
				this.gdshow=false
				this.nameshow = true
			},
			//打开更多
			gdgo(){
				this.gdshow=true
			},
			//关闭更多
			gdshowclose(){
				this.gdshow=false
			},
			//费率显示
			flgo(){
				if(!this.firstone){
					this.firstone=true
					this.flshow();
				}
				this.feilv=true
			},
			//跳转漏保主机
			lbgo(){
				uni.navigateTo({
					url: '/pages/Failsafe_host/Failsafe_host?projectId='+this.projectId+'&id='+this.areaId+'&isProportion='+this.isProportion+'&deviceSettings='+this.deviceSettings+'&areaName='+this.areaName+'&projectName='+this.projectName+'&marks='+this.marks,
				});
			},
			//跳转添加充电区
			addbtn(){
				 uni.navigateTo({
				        url: '/pages/add_charging_area/add_charging_area',
				 });
			
			},
			//跳转费率绑定
			ratebtn(){
				uni.navigateTo({
				       url: '/pages/rate_binding/rate_binding?projectId='+this.projectId+'&areaId='+this.areaId+'&addNew='+this.addNew+'&areaType='+this.areaType+'&isProportion=' + this.isProportion,
				});
			},
			mjgo(){
				uni.navigateTo({
				       url: '/pages/access_control_equipment/access_control_equipment?areaId='+this.areaId+'&projectName='+this.projectName+'&areaName='+this.areaName+'&marks='+this.marks+'&marks1='+this.marks1
				});
			},
			//修改名称提交
			namebtn(){
				let data={
					name:this.name,
					marks:this.marks1
				}
				this.$base.request1('manager/area'+'/'+this.areaId, 'PUT',data)
					.then(res => {
						if(res.data.code=='200'){
							uni.setStorageSync('typetwo',1)
							this.$toast.success(res.data.msg)
							// uni.navigateBack()
							this.areaName=this.name
							this.marks=this.marks1
							this.nameshow=false
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			//删除设备
			detailnum(id){
				if(this.deviceSettings=='1'){
					Dialog.confirm({
					  title: '标题',
					  message: '确认删除？'
					}).then(() => {
					  this.$base.request1('manager/equipment'+'/'+id, 'DELETE')
					  	.then(res => {
					  		if(res.data.code=='200'){
					  			this.$toast.success(res.data.msg)
					  			this.blist=[];
					  			this.onRefresh();
					  		}else{
					  			this.$toast.fail(res.data.msg)
					  		}
					  	})
					  	.catch(err => {
					  
					  	})
					}).catch(() => {
					  // on cancel
					});
				}else{
					this.$toast.fail('您暂无权限')
					return
				}
			},
			//修改极限参数
			csbtn(){
				let data={
					longTime:this.longTime,
					longPower:this.longPower
				}
				this.$base.request('manager/area/limit'+'/'+this.areaId, 'PUT',data)
					.then(res => {
						if(res.data.code=='200'){
							this.$toast.success(res.data.msg)
							uni.navigateBack()
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			//添加设备
			scan() {
				if(this.deviceSettings=='1'){
					let _this = this;
					let data={
						url:location.href.split('#')[0],
						appid:uni.getStorageSync('user').appid
					}
					_this.$base.request('configs', 'GET',data)
						.then(res => {
							if(res.data.code=='200'){
								var timestamp = res.data.data.timestamp;;
								var noncestr = res.data.data.noncestr;
								var signature = res.data.data.signature;
								var appId=res.data.data.appid;
								wx.config({
									debug : false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
									//debug : true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
									appId : appId, // 必填，公众号的唯一标识
									timestamp : timestamp, // 必填，生成签名的时间戳
									nonceStr : noncestr, // 必填，生成签名的随机串
									signature :signature,// 必填，签名，见附录1
									jsApiList: ['scanQRCode',] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
								});
								
								wx.ready(function() {
									wx.scanQRCode({
										needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
										scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
										success: function (res1) {
											var dk=res1.resultStr.split('/')
											var no=Number(dk.length)-2
											if(_this.areaType==0){
												if(dk[no].substr(-2,1)!=9){
													_this.deviceId=dk[no]
													_this.jkbtn();
												}else{
													_this.$toast.fail('充电柜设备请在充电柜充电区添加')
													return
												}
											}else{
												if(dk[no].substr(-2,1)!=9){
													_this.$toast.fail('充电柜充电区只能添加充电柜设备')
													return
												}else{
													_this.deviceId=dk[no]
													_this.jkbtn();
												}
											}
											
											
										}
									});
								});
							}else{
								this.$toast.fail(res.data.msg)
								return
							}
							
						})
						.catch(err => {
					
						})
					
				}else{
					this.$toast.fail('您暂无权限')
					return
				}
			},
			jkbtn(){
				let data={
					deviceId:this.deviceId,
					areaId:this.areaId
				}
				this.$base.request1('manager/equipment', 'POST',data)
					.then(res => {
						if(res.data.code=='200'){
							this.$toast.success(res.data.msg)
							this.blist=[];
							this.onRefresh();
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			//刷新
			shuxin(){
				this.blist=[];
				this.onRefresh();
			},
			// 设备列表分页
			loadMore() {
				if (this.refreshing) {
				  this.blist = [];
				  this.refreshing = false;
				}
				const that = this;
				let data = {
					pageNum:that.pageNum,
					pageSize:that.pageSize,
				}
				that.loading = true;
				that.$base.request1('manager/equipment/list'+'/'+this.areaId, 'GET', data)
					.then(res => {
						that.loading = false;
						if(res.data.data&&res.data.data.length>0){
							var arr=res.data.data
							for(var i=0;i<arr.length;i++){
								if(arr[i].portState){
									if(that.areaType==0){
										arr[i]["dknumber"]=arr[i].portState.split(',')
									}else{
										arr[i]["dknumber"]=arr[i].portState.split(',')
										arr[i]["dknumber1"]=arr[i].doorState
									}
									
								}else{
									arr[i]["dknumber"]=[]
								}
								
							}
							
							that.blist = that.blist.concat(arr);
							that.pageNum += 1;
						}else{
							that.finished = true;
						}
					})
					.catch(err => {
				
					})
			},
			 onRefresh() {
			      // 清空列表数据
			      this.finished = false;
				  this.pageNum=1
			      // 重新加载数据
			      // 将 loading 设置为 true，表示处于加载状态
			      this.loading = true;
			      this.loadMore();
			    },
			showclose(){
				this.feilv=false
			},
			//备注信息展开
			bbtn(){
				if(this.beitype==0){
					this.beitype=1
				}else if(this.beitype==1){
					this.beitype=0
				}
			},
