								// this.blackbtn()
							}else{
								this.$toast.fail(res.data.msg)
								return
							}
							
						})
						.catch(err => {
					
						})
				}).catch(() => {
					// on cancel
				});
				
			},
			//刷新设备信息
			shuabtn(){
				
				let data={
					deviceId:this.sbdeviceId
				}
				this.$base.request1('manager/equipment/reloadSimI', 'DELETE',data)
					.then(res => {
						if(res.data.code==200){
							this.timer = 3
							this.Verification = false; //点击button改变v-show的状态
							let auth_timer = setInterval(() => { //定时器设置每秒递减
								this.timer--; //递减时间
								if (this.timer <= 0) {
									this.$base.request1('manager/equipment'+'/'+this.sbid, 'GET')
										.then(res => {
											this.host={}
											this.equipment={}
											if(res.data.data.host){
												this.host=res.data.data.host[0]
											}
											if(res.data.data.equipment){
												this.equipment=res.data.data.equipment[0]
											}
										})
										.catch(err => {
									
										})
									this.Verification = true; //60s时间结束还原v-show状态并清除定时器
									clearInterval(auth_timer)
									// this.hkshow = false
								}
							}, 1000)
							
						}else{
							this.$toast.fail(res.data.msg)
							return
						}
					})
					.catch(err => {
				
					})
				
			},
			dnobtn(deviceId){
				uni.navigateTo({
					url: '../port_management/port_management?deviceId=' + deviceId 
				});
			},
			rembtn(){
				let data={
					id:this.remid,
					remark:this.upremarks
				}
				this.$base.request1('manager/equipment/editRemark', 'POST',data)
					.then(res => {
						if(res.data.code=='200'){
							this.remshow=false
							this.$toast.success(res.data.msg)
							this.blist=[];
							this.onRefresh();
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			//修改备注
			uprem(id,remarks){
				this.remid=id
				this.upremarks=remarks
				this.remshow=true
			},
			//组网
			zwbtn(id){
				this.$base.request1('manager/equipment/combinationNet/' +id, 'GET')
					.then(res => {
						if(res.data.code=='200'){
							this.$toast.success('操作成功')
							this.shuxin()
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
					})
			},
			
		}
	}
